<template>
  <div class="home py-3">
    <!--<div id="map" style="width: 100%; height: 800px;"></div>-->

    <h1 class="text-center mb-4">口罩哪裡買</h1>

    <div class="input-group input-group-lg mb-3">
      <div class="input-group-prepend">
        <button
          class="btn btn-outline-dark dropdown-toggle"
          type="button"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
        >{{ (from.search.type === 'address') ? '地址' : '藥局名稱' }}</button>
        <div class="dropdown-menu">
          <a class="dropdown-item" @click="from.search.type = 'address'">地址</a>
          <a class="dropdown-item" @click="from.search.type = 'name'">藥局名稱</a>
        </div>
      </div>
      <input type="text" class="form-control" v-model="from.search.value" @keyup.enter="onSearch" />
      <div class="input-group-append">
        <button class="btn btn-info" type="button" @click="onSearch">搜尋</button>
      </div>
    </div>

    <div class="row">
      <div class="col-12 mb-5">
        <div class="form-inline">
          <div class="input-group mr-2 mb-1">
            <div class="input-group-prepend">
              <div class="input-group-text">
                <input type="radio" v-model="from.filter" value @change="onFilter" />
              </div>
            </div>
            <div class="input-group-append">
              <button class="btn btn-secondary" type="button" @click="onClickFilter('')">清除條件</button>
            </div>
          </div>
          <div class="input-group mr-2 mb-1">
            <div class="input-group-prepend">
              <div class="input-group-text">
                <input type="radio" v-model="from.filter" value="q_suff" @change="onFilter" />
              </div>
            </div>
            <div class="input-group-append">
              <button class="btn btn-secondary" type="button" @click="onClickFilter('q_suff')">庫存充足</button>
            </div>
          </div>
          <div class="input-group mr-2 mb-1">
            <div class="input-group-prepend">
              <div class="input-group-text">
                <input type="radio" v-model="from.filter" value="q_no_suff" @change="onFilter" />
              </div>
            </div>
            <div class="input-group-append">
              <button
                class="btn btn-secondary"
                type="button"
                @click="onClickFilter('q_no_suff')"
              >庫存有限</button>
            </div>
          </div>
          <div class="input-group mr-2 mb-1">
            <div class="input-group-prepend">
              <div class="input-group-text">
                <input type="radio" v-model="from.filter" value="a_suff" @change="onFilter" />
              </div>
            </div>
            <div class="input-group-append">
              <button
                class="btn btn-secondary"
                type="button"
                @click="onClickFilter('a_suff')"
              >成人庫存 > 30</button>
            </div>
          </div>
          <div class="input-group mr-2 mb-1">
            <div class="input-group-prepend">
              <div class="input-group-text">
                <input type="radio" v-model="from.filter" value="c_suff" @change="onFilter" />
              </div>
            </div>
            <div class="input-group-append">
              <button
                class="btn btn-secondary"
                type="button"
                @click="onClickFilter('c_suff')"
              >小孩庫存 > 30</button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div v-if="isLoading" class="row mt-5">
      <div class="col-12">
        <div class="spinner-border text-info" role="status">
          <span class="sr-only">Loading...</span>
        </div>
      </div>
    </div>
    <div v-else class="row mt-4">
      <div class="col-md-6 col-lg-4" v-for="store in stores" :key="store.code">
        <div class="card mb-3">
          <div class="card-body">
            <span
              class="badge px-2 py-1 mb-3"
              :class="getMaskTotalLevel(store.adult_count + store.child_count)"
            >{{ getMaskTotalLevelText(store.adult_count + store.child_count) }}</span>
            <h5 class="card-title">{{ store.name }}</h5>
            <h6 class="card-subtitle mt-1 mb-2 text-muted">{{ store.address }}</h6>
            <h6 class="card-subtitle mt-1 mb-2 text-muted">{{ store.phone }}</h6>
            <p v-if="store.distance">距離: {{ store.distance }} 公尺</p>
            <button type="button" class="btn btn-info mr-2 mb-2">
              成人
              <span class="badge badge-light ml-1">{{ store.adult_count }}</span>
              <span class="sr-only">{{ store.adult_count }}</span>
            </button>
            <button type="button" class="btn btn-info mb-2">
              小孩
              <span class="badge badge-light ml-1">{{ store.child_count }}</span>
              <span class="sr-only">{{ store.child_count }}</span>
            </button>
            <div class="d-flex flex-row justify-content-between align-items-center mt-3">
              <small class="text-muted">{{ getUpdateTime(store.updated_time) }}更新</small>
              <a
                class="btn btn-link"
                target="_blank"
                :href="`https://www.google.com.tw/maps/dir/${store.lat},${store.lng}`"
              >導航至此 >></a>
            </div>
          </div>
          <div
            class="card-footer"
            style="cursor: pointer;"
            @click="showModal(store.name, store.business_hours)"
          >查看營業時間</div>
        </div>
      </div>
    </div>

    <modal v-if="isShowModal" :width="'450px'" :hideFooter="true">
      <div
        slot="header"
        class="d-flex flex-row w-100 justify-content-between align-items-center px-2"
      >
        <b>{{ modelStore }} - 營業時間</b>
        <button type="button" class="close" aria-label="Close" @click="isShowModal = false">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div slot="body">
        <ul class="list-group list-group-flush">
          <li class="list-group-item py-2" v-for="(bh, k) in businessHours" :key="'bh' + k">{{ bh }}</li>
        </ul>
      </div>
    </modal>
  </div>
</template>

<script>
// import mapboxgl from "mapbox-gl/dist/mapbox-gl.js";

const Modal = () => import("@/components/Modal");

export default {
  name: "home",
  components: {
    Modal
  },
  data() {
    return {
      isLoading: false,
      coords: {
        lat: 0,
        lng: 0
      },
      map: null,
      from: {
        search: {
          type: "address",
          value: ""
        },
        filter: ""
      },
      orgStores: [],
      stores: [],
      isShowModal: false,
      businessHours: [],
      modelStore: ""
    };
  },
  mounted() {
    // this.mapInit();

    this.searchLocation();
  },
  methods: {
    async searchLocation() {
      const hasLoc = await this.getLocation();
      if (!hasLoc) return;

      this.isLoading = true;

      const res = await this.$api.location(this.coords.lng, this.coords.lat);

      this.orgStores = res.data;
      this.stores = res.data;

      this.from.filter = "";
      this.onFilter();

      this.isLoading = false;
    },
    async onSearch() {
      this.isLoading = true;

      const res = await this.$api.search(
        this.from.search.type,
        this.from.search.value,
        this.coords.lng,
        this.coords.lat
      );

      this.orgStores = res.data;
      this.stores = res.data;

      this.from.filter = "";
      this.onFilter();

      this.isLoading = false;
    },
    getLocation() {
      return new Promise((resolve, reject) => {
        if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(p => {
            this.coords.lng = p.coords.longitude;
            this.coords.lat = p.coords.latitude;
            resolve(true);
          });
        } else {
          reject(false);
        }
      });
    },
    /*async mapInit() {
      const hasLoc = await this.getLocation();
      if (!hasLoc) return;

      mapboxgl.accessToken =
        "pk.eyJ1Ijoicm95YWxtYXIiLCJhIjoiY2p2NTV2N3YwMnkzdDN5cGZuMjgzNm56ayJ9.nFhZNElKWpm8zkQ-9mQM0Q";

      this.map = new mapboxgl.Map({
        container: "map",
        style: "mapbox://styles/mapbox/streets-v9?optimize=true",
        center: [this.coords.lng, this.coords.lat],
        minZoom: 6,
        maxZoom: 18,
        zoom: 13
      });

      this.setMarker(this.coords, { color: "#e91e63" });
    },
    setMarker(coords, options = {}, popup = null) {
      new mapboxgl.Marker(options).setLngLat(coords).addTo(this.map);
    },*/
    onClickFilter(f) {
      this.from.filter = f;
      this.onFilter();
    },
    onFilter() {
      this.stores = this.orgStores;

      if (this.from.filter === "q_suff") {
        this.stores = this.stores.filter(store => {
          let count = store.adult_count + store.child_count;
          return count >= 50;
        });
      } else if (this.from.filter === "q_no_suff") {
        this.stores = this.stores.filter(store => {
          let count = store.adult_count + store.child_count;
          return count >= 20 && count < 50;
        });
      } else if (this.from.filter === "a_suff") {
        this.stores = this.stores.filter(store => {
          return store.adult_count >= 30;
        });
      } else if (this.from.filter === "c_suff") {
        this.stores = this.stores.filter(store => {
          return store.child_count >= 30;
        });
      }

      this.storeSort();
    },
    storeSort() {
      if (!this.coords.lng || !this.coords.lat) return;

      this.stores.sort((a, b) => {
        return a.distance - b.distance;
      });
    },
    getMaskTotalLevel(count) {
      if (count >= 50) return "badge-success";
      else if (count >= 20 && count < 50) return "badge-warning";
      else return "badge-danger";
    },
    getMaskTotalLevelText(count) {
      if (count >= 50) return "# 庫存充足";
      else if (count >= 20 && count < 50) return "# 庫存有限";
      else return "# 庫存不足";
    },
    getUpdateTime(createtime) {
      let now = moment(new Date()).format('X');
      let updateTime = moment(createtime).format('X');

      var limit = now - updateTime;
      var content = "";
      if (limit < 60) {
        content = limit + " 秒前";
      } else if (limit >= 60 && limit < 3600) {
        content = Math.floor(limit / 60) + " 分鐘前";
      } else {
        content = Math.floor(limit / 3600) + " 小時前";
      }

      return content;
    },
    showModal(name, bhs) {
      this.modelStore = name;
      this.businessHours = bhs || [];
      this.isShowModal = true;
    }
  }
};
</script>

<style lang="scss" scoped>
.close {
  &:focus {
    outline: none;
  }
}
</style>
